const utils = require('./utils.js');

function getProfile(id) {
    const clientPromise = stitch.StitchClientFactory.create('wwe-champions-guide-pgwcj');
    clientPromise.then(client => {
        const db = client.service('mongodb', 'mongodb-atlas').db('wwe-champions');
        client.login().then(() =>
            db.collection('players').findOne({_id: {"$oid": id}})
        ).then(doc => {
            hideLoading();
            if (doc) {
                displayResult(doc)
            } else {
                displayError();
            }
        }).catch(err => {
            console.error("Invalid request");
            console.error(err);
            hideLoading();
            displayError();
        });
    });
}

function hideLoading() {
    $("#loading").hide();
}

function displayError() {
    $("#playerName").text("Error loading profile!");
}

function displayResult(item) {
    var $tablebody = $("<tbody/>");
    item.feudScores.sort( function(a, b) {
        return a.getTime() - b.getTime();
    });
    item.feudScores.forEach(function(score){
        var period = (score.ffEndTime - score.ffStartTime) / (1000 * 60 * 60);
        var dateString = score.ffStartTime.getUTCDate() + "/" + (score.ffStartTime.getUTCMonth()+1) + "/" + score.ffStartTime.getUTCFullYear() + " (" + period + ")";
        $tablebody.append(
            $("<tr/>")
            .append(
                $("<td/>", {text: dateString})
            )
            .append(
                $("<td/>", {text: score.score || 0})
            )
            .append(
                $("<td/>", {text: score.faction})
            )
            .append(
                $("<td/>", {text: score.rank || 0})
            )
        )
    });

    $("#playerName").text(item.name);
    $("#feudResults").empty();
    $("#feudResults").append( $("<h3/>", {text: "Last Feuds Scores"}) );
    $("#feudResults")
    .append(
        $("<table/>", {class: "table"})
        .append(
            $("<thead/>")
            .append(
                $("<tr/>")
                .append(
                    $("<th/>", {text: "Feud"})
                )
                .append(
                    $("<th/>", {text: "Score"})
                )
                .append(
                    $("<th/>", {text: "Faction"})
                )
                .append(
                    $("<th/>", {text: "Rank"})
                )
            )
        )
        .append(
            $tablebody
        )
    )

}

$(document).ready(function () {
    var id = utils.getUrlParameter('id');
    if (id) {
        console.log("Getting profile for " + id);
        getProfile(id);
    }
});


